@import "main";

$breakpoints: (phone: 640px, 
               tablet: 768px, 
               desktop: 1024px) !default;

body {
  background-image: url("/images/back.jpg");
  background-repeat: no-repeat;
  background-attachment: fixed;
}

h1 {
  margin-top: 100px;
  margin-bottom: 70px;
  font-family: 'Lucida Sans';
  color: #fff;
  text-shadow: -1px 0 black, 0 1px black, 1px 0 black, 0 -1px black;
}

.card__content {
  text-align: justify;
	position: relative;
	width: 100%;
	max-width: 80%;
	height: auto;
	margin: 5%;
	padding: 4%;
	background: #fff;
}

.card__caption {
	font-size: 2em;
	max-width: 800px;
	margin: 0 auto;
	padding: 35px 30px 0px;
}

.card__title {
	font-size: 1.5em;
	line-height: 1;
	margin: 5px 0 0 0;
	color: #3b393d;
}

.card__copy {
	font-size: 1.25em;
	max-width: 900px;
	margin: 0 auto;
	padding: 25px 30px 100px;
	color: #3b393d;
}

.wrapper {
  margin: 4%;
}

.wrapper-card {
  position: relative;
  display: inline-block;
  margin: 1%;
}

.card-picture {
  border-radius: 4%;
  border: 1px solid black;
  height: auto;
  width: 300px;
}

.card-picture-section {
  border: 1px solid black;
  width: 90%;
  height: 20em;
  //height: 300px;
  overflow: hidden;
}

.card-picture-section img {
  width: 100%;
  height: auto;
  margin: -10% 0 0 0;
}

.plant-name {
  font-family: 'Lucida Sans';
  font-size: x-large;
  color: #fff;
  text-shadow: -1px 0 black, 0 1px black, 1px 0 black, 0 -1px black;
  margin-top: -20px;
  margin-left: 15px;
}

@media (max-width: 480px) {
  .card-picture-section {
      height: 9em;
  }

  .wrapper {
    margin: 1%;
  }

  .card__title {
    font-size: 0.8em;
    line-height: 1;
    margin: 1% 0 0 0;
    color: #3b393d;
  }  

  .card__caption {
    font-size: 2em;
    max-width: 80%;
    margin: 0 auto;
    padding: 6%;
  }

  .card__copy {
    font-size: 0.7em;
    max-width: 110%;
    margin: 0 auto;
    padding: 1%;
    color: #3b393d;
  }

  .card__content {
    text-align: justify;
    position: relative;
    width: 100%;
    max-width: 80%;
    height: auto;
    margin: 5%;
    padding: 4%;
    background: #fff;
  }
}